/* istanbul ignore file */
import React, { Component } from 'react';
import styled from 'styled-components';

const Button = styled.button`
  font-weight: 700;
  display: inline-block;
  text-align: center;
  padding: 12px 12px;
  font-size: 14px;
  width: 160px;
  position: relative;
  transition: all 0.2s ease;
  width: 50%;
  outline: none;
  margin-bottom: 24px;
  text-transform: none;
  background: ${props =>
    props.active
      ? 'rgb(255, 255, 255) !important'
      : 'rgb(244, 244, 244) !important'};
  color: ${props =>
    props.active ? 'rgb(0, 0, 0) !important' : 'rgb(136, 136, 136) !important'};
  &:hover {
    background: ${props =>
      props.active
        ? 'rgb(255, 255, 255) !important'
        : 'rgb(244, 244, 244) !important'};
    color: ${props =>
      props.active
        ? 'rgb(0, 0, 0) !important'
        : 'rgb(136, 136, 136) !important'};
  }
  border: ${props =>
    props.active
      ? '2px solid rgb(0, 163, 173) !important'
      : '2px solid rgb(170, 170, 170) !important'};
  z-index: ${props => (props.active ? '2' : '1')};
  margin-left: -2px;
  @media only screen and (min-width: 768px) {
    max-width: 162px;
  }
`;

class ToggleButton extends Component {
  constructor() {
    super();

    this.state = {};
  }

  componentDidMount() {}

  render() {
    return <Button {...this.props}>{this.props.children}</Button>;
  }
}

export default ToggleButton;
