/* istanbul ignore file */
import React from 'react';

import ToggleButton from '../ToggleButton';
import Anchor from '../Anchor';

import Styles from "./styles";

class DynamicScreen extends React.Component {
  constructor(props) {
      super(props);
      this.state = {
        leftActive: false,
        rightActive: false
      };
      this.goTo = this.goTo.bind(this);
      this.changeScreens = this.changeScreens.bind(this);
    }

    changeScreens(list, activeStates) {
      this.props.showScreens(list);
      this.setState({
        leftActive: activeStates[0],
        rightActive: activeStates[1]
      });
    }

    goTo(url, wasLeft) {
      this.setState({
        'leftActive': wasLeft,
        'rightActive': !wasLeft
      });
      if (url.indexOf('/') > -1) {
        window.location.href = url;
      } else {
        this.props.showScreen(url);
      }
    }

    render() {
      let allDots = [];
      for (let i = 0; i < this.props.steps; i++) {
        allDots.push(<Styles.Dot key={i} active={this.props.thisStep >= i+1}>{i+1}</Styles.Dot>);
      }
      if (typeof this.props.visibleScreens !== 'undefined' && this.props.visibleScreens.split(' ').indexOf(this.props.id) === -1) {
        return <></>
      }
      if ((typeof this.props.showSimReplacement !== 'undefined' && !this.props.showSimReplacement)) {
        return <></>
      }
      return (<>
        {this.props.formType ? <>
          <Anchor onClick={() => this.props.goBack(this.props.thisStep)}>&lt; Back to previous page</Anchor>
          {!this.state.hideDots && <Styles.Dots>
            {allDots}</Styles.Dots>}
          <Styles.Title>{this.props.title}</Styles.Title>
        </> : ''}
        {this.props.showSuccessIcon ? <>
          <Styles.SuccessTick></Styles.SuccessTick>
          <Styles.Centre>{this.props.children}</Styles.Centre>
        </> : this.props.children}
        {this.props.leftTab ? <>
          <ToggleButton active={this.state.leftActive} onClick={() => this.props.leftTabUrl ? this.goTo(this.props.leftTabUrl, true) : this.changeScreens(this.props.leftTab, [true, false])}>{this.props.leftTabText}</ToggleButton>
        </> : ''}
        {this.props.rightTab ? <>
          <ToggleButton active={this.state.rightActive} onClick={() => this.props.rightTabUrl ? this.goTo(this.props.rightTabUrl, false) : this.changeScreens(this.props.rightTab, [false, true])}>{this.props.rightTabText}</ToggleButton>
        </> : ''}
      </>);
    }
}

export default DynamicScreen;
